<template>
  <div>
    <el-row>
      <el-col span="8" offset="8">
        <ul class="regul">
          <li>
            <el-input
              v-model="reginfo.user_id"
              placeholder="请输入用户名"
              prefix-icon="el-icon-user-solid"
            ></el-input>
          </li>
          <li>
            <el-input
              v-model="reginfo.user_email"
              placeholder="请输入邮箱地址"
              prefix-icon="el-icon-user-solid"
            ></el-input>
          </li>
          <li>
            <el-input v-model="reginfo.user_key" placeholder="请输入密码" prefix-icon="el-icon-s-tools"></el-input>
          </li>
          <li>
            <el-button @click="doreg" type="primary" icon="el-icon-edit">确认注册</el-button>
          </li>
        </ul>
      </el-col>
    </el-row>
  </div>
</template>

<script>
import axios from "axios";

export default {
  data() {
    return {
      reginfo: {
        user_id: "",
        user_key: ""
      },
      reg_loading: false,
      input_is_err: true,
      id_err: "",
      key_err: ""
    };
  },
  methods: {
    input_is_ok() {
      this.input_is_err = false;
      if (this.reginfo.user_id.length == 0) {
        this.$message("用户名不可为空");
        this.input_is_err = true;
      }

      if (this.reginfo.user_key.length < 6) {
        this.$message("密码至少6位");
        this.input_is_err = true;
      } else {
        if (/[^\w\.\/]/gi.test(this.reginfo.user_key)) {
          //正则为 查找非 字母 . /
          this.$message("密码不可包含汉字");
          this.input_is_err = true;
        }
      }
    },
    doreg() {
      this.input_is_ok();
      if (!this.input_is_err) {
        this.$confirm("即将注册，请牢记密码后点击确定。", "提示", {
          confirmButtonText: "确定",
          cancelButtonText: "取消",
          type: "warning"
        }).then(() => {
          this.reg_loading = true;
          axios({
            url: require("@/config").axios.reg,
            method: "post",
            data: {
              name: this.reginfo.user_id,
              key: this.reginfo.user_key,
              token: sessionStorage.getItem("token")
            }
          }).then(re => {
            this.$alert(`您的登录ID为 ${re.data.uid}，请记录。`, "注册成功", {
              confirmButtonText: "确定",
              callback: () => {
                this.$router.push("/");
              }
            });
          });
        });
      }
    }
  }
};
</script>

<style scoped>
div button {
  width: 100%;
}

.regul li {
  padding: 8px 0;
}
</style>